.contenedor-gato {
    margin: 0px 0px 10px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: rgb(25, 70, 70);
    border: 5px solid aqua;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
  }

.contenedor-ImagenGato {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(33, 122, 122);
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
}
.imagenGato {
  width: 100%;
  border: 7px solid  rgb(4, 39, 39);
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
}

.contenedor-DatosTextoGato {
  height: 130px;
  padding: 0px 0px 0px 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  border-top: 5px double red;
  border-left: 5px double red;
  border-bottom: 5px double red;
}

.contenedor-DatosGato {
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  border-top: 5px double red;
  border-right: 5px double red;
  border-bottom: 5px double red;
}

.contenedor-VidaHabilidadesGato {
  height: 160px;  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}